.widget {

	// Core widgets.
	&--archives {}
	&--calendar {}
	&--categories {}
	&--custom-html {}
	&--media-audio {}
	&--media-gallery {}
	&--media-image {}
	&--media-video {}
	&--meta {}
	&--nav-menu {}
	&--pages {}
	&--recent-comments {}
	&--recent-posts {}
	&--rss {}
	&--text {}
	&--search {}
	&--tag-cloud {}

	// Widget titles.
	&__title {
		position: relative;
		padding-bottom: 0.58em;
		font-size: 1.125rem;
		text-align: center;
		border-bottom: 1px solid color( 'coral-red' );

		&::after {
			content: "\2666";
		    position: absolute;
		    top: 1.4em;
		    left: calc( 50% - 15px );
		    background: #fff;
		    width: 30px;
		    color: color( 'coral-red' );
		    text-align: center;
		}
	}
}



