@mixin heading-primary {
	font-size: 1.75rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	text-align: center;
	font-weight: 900;
	line-height: 1.5;

	@media only screen and ( min-width: 950px ) {
		font-size: 2.75rem;
		margin-left  : -150px;
		margin-right : -150px;
		max-width    : 950px;
	}
}

@mixin meta {
	font-size: 1rem;
	color: color( 'silver-chalice' );
}



