////
/// App content component.
///
/// This component generally wraps the `<main>` section and the sidebar.
///
/// @group  Components
/// @author Justin Tadlock <justintadlock@gmail.com>
/// @link   https://themehybrid.com/themes/exhale
////

.app-content {
	overflow: hidden;
	position:  relative;
	max-width: 100%;
	margin:    0 auto;
	padding:   0 1.5em;
	word-wrap  : break-word;
	word-break : normal;
	font-size: 1.1875rem;
	line-height : 1.68;
	box-sizing: border-box;
}



