////
/// Entry component.
///
/// @group  Components
/// @author Justin Tadlock <justintadlock@gmail.com>
/// @link   https://themehybrid.com/themes/exhale
////

.entry {

	// Type of entry/post.
	&--type-post {}

	&--type-page {}

	// Sticky posts.
	&.sticky {}

	// Featured image.
	&__image {
		margin: 1.414em 0 0.5em;
	}

	// Header wrapper.
	&__header {}

	// Entry title.
	&__title {
		font-size: 1.5rem;
	}

	// Subtitle.
	&__subtitle {
		margin-top: -0.83em;
		margin-bottom: 1.66em;
		font-size: 1.25rem;
		font-style: italic;
		color: #bbb;
	}

	// Full entry content.
	&__content {}

	// Summary/Excerpt.
	&__summary {}

	// Meta wrappers.
	&__byline,
	&__footer {
		@include meta;
	}

	&__byline {
		margin: 1em 0 0.5em 0;
	}

	&__footer {
		margin: 1.5rem 0;
		padding-top: 1.5rem;
		text-align: center;
		border-top: 1px solid #f1f1f1;
	}

	&__topics {
		a {
			margin: 0 0.25rem;
		}
	}
}

.single .entry {

	&__header {
		margin-bottom: 3rem;
		text-align: center;
	}

	&__title {
		@include heading-primary;
	}

	&__byline {
		margin: 0;
		padding-top: 1.5rem;
		border-top: 1px solid #f1f1f1;
	}
}



